* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
  text-decoration: none;
  list-style: none;
}

::selection {
  background-color: rgb(38, 157, 135);
  color: rgb(255, 255, 255);
}

html {
  scroll-behavior: smooth;
}

.container {
  max-width: 1220px;
  margin: 0 auto;
  padding: 0 15px;
}

.title {
  font-size: 24px;
  font-weight: 600;
}

body {
  background-color: rgb(36, 29, 50);
  color: rgb(255, 255, 255);
}

body::-webkit-scrollbar {
  background-color: rgb(75, 75, 75);
  width: 12px;
}

body::-webkit-scrollbar-thumb {
  background-color: rgb(255, 255, 255);
}

body::-webkit-scrollbar-thumb:hover {
  background-color: rgb(185, 185, 185);
}

header {
  background-image: url("../images/header__image.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.header__section {
  position: fixed;
  top: 0;
  left: 50%;
  width: 101%;
  height: 90px;
  transform: translateX(-50%);
  z-index: 9999;
  transition: 0.2s ease;
}

.header__section.active-navbar {
  background-color: rgb(36, 29, 50);
  box-shadow: 0 0 7px rgba(223, 223, 223, 0.2);
}

.header__navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 90px;
}

.header__navbar .nav-links ul {
  display: flex;
}

.header__navbar .nav-links ul li a {
  color: #ffffff;
  margin: 0 23px;
  padding: 8px 1px;
  font-size: 18px;
  position: relative;
  transition: 0.1s ease;
}

.header__navbar .nav-links ul li a::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
  width: 0;
  height: 1px;
  background-color: rgb(38, 157, 135);
  transition: 0.1s ease;
}

.header__navbar .nav-links ul li a:hover {
  color: rgb(38, 157, 135);
}

.header__navbar .nav-links ul li a:hover::after {
  width: 100%;
}

.header__navbar .nav-links ul li a.link-active {
  color: rgb(38, 157, 135);
}

.header__navbar .nav-links ul li a.link-active::after {
  width: 100%;
}

.header__navbar .burger .bar {
  width: 28px;
  height: 3px;
  background-color: rgb(255, 255, 255);
  margin: 6px 0;
  border-radius: 10px;
}

.header__navbar .burger {
  cursor: pointer;
  display: none;
}

.header__navbar .navside {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.975);
  padding: 136px 60px;
  transform: translateY(-100%);
  transition: 0.2s ease;
}

.header__navbar .navside.nav-active {
  transform: translateY(0);
}

.header__navbar .navside .close__btn .bar {
  position: absolute;
  top: 45%;
  left: 18%;
  transform: translate(-50%, -50%);
  width: 28px;
  height: 3px;
  background-color: rgb(255, 255, 255);
  border-radius: 10px;
}

.header__navbar .navside .close__btn {
  position: absolute;
  top: 26px;
  right: 10px;
  width: 40px;
  height: 40px;
  cursor: pointer;
}

.header__navbar .navside .close__btn .bar1 {
  transform: rotate(-45deg);
}

.header__navbar .navside .close__btn .bar2 {
  transform: rotate(45deg);
}

.header__navbar .navside ul li {
  display: block;
  margin: 30px 0;
}

.header__navbar .navside ul li a {
  font-size: 18px;
  font-weight: 400;
  padding: 8px 0;
  position: relative;
  color: rgb(255, 255, 255);
  transition: 0.1s ease;
}

.header__navbar .navside ul li a::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
  width: 0;
  height: 1px;
  background-color: rgb(38, 157, 135);
  transition: 0.1s ease;
}

.header__navbar .navside ul li a:hover {
  color: rgb(38, 157, 135);
  padding: 10px 0;
}

.header__navbar .navside ul li a:hover::after {
  width: 100%;
}

.header__navbar .navside .nav_social__links {
  display: flex;
  margin-top: 50px;
}

.header__navbar .navside .nav_social__links a {
  color: rgb(255, 255, 255);
  margin-left: 10px;
  font-size: 20px;
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 45px;
  height: 45px;
  transition: 0.2s ease;
}

.header__navbar .navside .nav_social__links a:nth-child(1) {
  margin-left: 0;
}

.header__navbar .navside .nav_social__links a:hover {
  border: 1px solid rgb(38, 157, 135);
}

.header__content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 250px;
}

.header__content .header__content_info {
  font-size: 32px;
  color: rgb(255, 255, 255);
}

.header__content .header__content_info .info span {
  color: rgb(38, 157, 135);
}

.header__content .header__content_info .name {
  font-size: 40px;
  font-weight: 700;
  margin: 8px 37px;
}

.header__content .header__content_image {
  max-width: 600px;
}

.header__content .header__content_image img {
  width: 100%;
}

/* =================== main ======================= */

.toUpBtn {
  position: fixed;
  top: 120%;
  right: 2%;
  opacity: 0;
  transition: 0.2s ease;
  z-index: 555;
}

.toUpBtn a {
  font-size: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 60px;
  height: 60px;
  background-color: rgba(38, 157, 135, 0.685);
  color: rgb(255, 255, 255);
  border-radius: 50%;
  transition: 0.2s ease;
}

.toUpBtn a:hover {
  background-color: rgb(38, 157, 135);
  color: rgb(36, 29, 50);
}

.toUpBtn.show-toUpBtn {
  position: fixed;
  top: 85%;
  opacity: 1;
}

.about__section {
  padding-top: 180px;
}

.about__items {
  display: flex;
  justify-content: space-between;
  align-items: center;
  column-gap: 20px;
}

.about__items .about__image_item {
  max-width: 600px;
}

.about__items .about__image_item img {
  width: 100%;
}

.about__items .about__text_item {
  max-width: 470px;
}

.about__items .about__text_item .about__text {
  font-size: 18px;
  line-height: 27px;
  margin-top: 36px;
}

.about__items .about__text_item .about__text_info {
  font-size: 18px;
  margin-top: 36px;
  line-height: 27px;
}

.about__items .about__text_item span {
  margin-top: 30px;
}

.skills__section {
  padding-top: 180px;
}

.skills__items {
  display: flex;
  justify-content: center;
  margin-top: 36px;
}

.skills__items ul {
  display: flex;
  align-items: center;
  column-gap: 57px;
}

.skills__items ul li {
  font-size: 32px;
  font-weight: 800;
}

.skills__items ul .border_ {
  width: 136px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
}

.css__lists {
  text-align: center;
  margin-right: 84px;
  margin-top: 30px;
}

.css__lists .select_border_ {
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
  max-width: 51px;
  margin: 37px auto;
  transform: rotate(90deg);
}

.css__lists p {
  font-size: 18px;
  font-weight: 300;
  margin-top: 8px;
  color: rgba(255, 255, 255, 0.7);
}

.projects__section {
  padding-top: 180px;
}

.project__items {
  margin-top: 40px;
}

.project__items .project__item {
  position: relative;
  border-radius: 20px;
  overflow: hidden;
  transition: 0.2s ease;
}

.project__items .project__item img {
  width: 100%;
  height: 100%;
  border-radius: 20px;
  transition: 0.2s ease;
}

.project__items .project__item .view_btn {
  position: absolute;
  top: 170%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: 0.2s ease;
}

.project__items .project__item .view_btn .viewBtn {
  padding: 18px 38px;
  font-size: 18px;
  font-weight: 500;
  background: none;
  color: rgb(255, 255, 255);
  border-radius: 10px;
  border: 1px solid rgb(255, 255, 255);
  transition: 0.2s ease;
}

.project__items .project__item .view_btn .viewBtn:hover {
  background-color: rgb(38, 157, 135);
  color: rgb(255, 255, 255);
  border: 1px solid rgb(38, 157, 135);
}

.project__items .project__item:hover img {
  transform: scale(1.1);
  filter: brightness(60%);
}

.project__items .project__item:hover .view_btn {
  position: absolute;
  top: 50%;
}

.project__items .swiper-button-next,
.project__items .swiper-button-prev {
  width: 30px;
  height: 60px;
  color: #ffffff;
  border-radius: 4px;
  text-shadow: 2px 0 7px rgb(0, 0, 0);
  transition: 0.2s ease;
}

.project__items .swiper-button-next:hover,
.project__items .swiper-button-prev:hover {
  color: rgb(38, 157, 135);
}

.footer__section {
  margin-top: 132px;
  padding: 80px 0 40px;
  border-top: 1px solid rgba(255, 255, 255, 0.3);
}

.footer__items {
  display: flex;
  justify-content: space-between;
}

.footer__items .footer__logo {
  max-width: 130px;
}

.footer__items .footer__logo img {
  width: 100%;
}

.footer__items .footer__links ul {
  margin-top: 24px;
}

.footer__items .footer__links ul li a {
  display: block;
  font-size: 16px;
  margin: 15px 0;
  color: rgba(255, 255, 255, 0.5);
  transition: 0.2s ease;
}

.footer__items .footer__links ul li a:hover {
  color: rgb(255, 255, 255);
}

.footer__items .footer__links .links_title {
  color: rgb(255, 255, 255);
  font-size: 18px;
  font-weight: 600;
}

.footer__items .footer__contact ul {
  margin-top: 24px;
}

.footer__items .footer__contact ul li {
  margin: 15px 0;
  color: rgba(255, 255, 255, 0.5);
  transition: 0.2s ease;
}

.footer__items .footer__contact ul li:hover {
  color: rgb(255, 255, 255);
}

.create {
  text-align: center;
  margin-top: 40px;
}

.create a {
  color: rgba(255, 255, 255, 0.5);
  max-width: 210px;
  margin: 0 auto;
  transition: 0.2s ease;
}

.create a:hover {
  color: rgb(255, 255, 255);
}

@media screen and (min-width: 631px) {
  .header__navbar .navside {
    position: absolute;
    top: -990%;
  }
}

@media screen and (max-width: 1130px) {
  .header__content .header__content_info {
    min-width: 488px;
  }
}

@media screen and (max-width: 930px) {
  .header__content {
    padding-top: 180px;
    display: flex;
    flex-direction: column;
    row-gap: 30px;
  }

  .header__content .header__content_info {
    min-width: 0;
  }

  .about__items {
    display: flex;
    flex-direction: column-reverse;
    row-gap: 30px;
  }

  .skills__container {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .skills__container .skills__items ul {
    display: flex;
    flex-direction: column;
    row-gap: 54px;
    text-align: center;
  }

  .skills__container .skills__items ul .border_ {
    transform: rotate(90deg);
    width: 86px;
  }

  .skills__container .css__lists {
    position: relative;
    margin-right: 0;
  }

  .skills__container .css__lists::after {
    content: "";
    position: absolute;
    top: 50%;
    left: -19%;
    transform: translate(-50%, -50%);
    width: 51px;
    height: 1px;
    background-color: rgba(255, 255, 255, 0.5);
  }

  .skills__container .css__lists .select_border_ {
    display: none;
  }
}

@media screen and (max-width: 840px) {
  .project__items {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
  }
}

@media screen and (max-width: 768px) {
  .footer__items {
    display: flex;
    flex-direction: column;
    row-gap: 30px;
  }

  .footer__items .footer__links .links_title {
    display: flex;
    align-items: center;
  }
}

@media screen and (max-width: 630px) {
  .header__navbar .nav-links {
    display: none;
  }

  .header__navbar .burger {
    display: block;
  }
}

@media screen and (max-width: 540px) {
  .project__items .swiper-button-next::after,
  .project__items .swiper-button-prev::after {
    font-size: 30px;
  }

  .header__content .header__content_info {
    font-size: 27px;
  }

  .header__content .header__content_info .name {
    font-size: 30px;
  }
}

@media screen and (max-width: 428px) {
  .header__content .header__content_info {
    font-size: 20px;
  }

  .header__content .header__content_info .name {
    font-size: 20px;
    margin-left: 10px;
  }

  .skills__container {
    column-gap: 20px;
  }

  .skills__container .skills__items ul li {
    font-size: 20px;
  }

  .skills__container .skills__items ul .border_ {
    width: 70px;
  }

  .project__items .project__item .view_btn .viewBtn {
    padding: 18px 25px;
    font-size: 16px;
  }
}

@media screen and (max-width: 340px) {
  .project__items .project__item .view_btn .viewBtn {
    padding: 18px 20px;
    font-size: 14px;
  }

  .footer__items .footer__contact ul li {
    font-size: 14px;
  }
}